.nav-header {
    width: 100%;
    margin: 0 auto;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 0.4em;
    font-weight: bold;
    position: relative;
    transform: translateZ(0)
}

.nav-header-line {
    display: block;
