}

.nav-link:hover::after {
    transform: scaleY(1)
}

.nav-link::after {
    content: "";
    background-color: #fff;
    width: 100%;
    height: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: scaleY(0);
    transform-origin: 0 100%;
    transition: all 0.25s ease-in-out;
    z-index: -1
}

