        width:100%;
        height: 480px
    }
}

.compositions-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #000;
    opacity: 0;
    z-index: 2000;
    display: none
}

.composition-source {
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    z-index: 3001;
    display: none;
    text-align: left;
    color: #000;
    box-sizing: border-box;
    overflow-y: scroll;
    padding-left: 50%
}

@media (max-width: 480px) {
    .composition-source {
        -webkit-overflow-scrolling: touch;
    }
}

@media (max-width: 940px) {
    .composition-source {
        padding-left:0;
        padding-top: 500px;
        margin-top: 0;
    }
}

@media (max-height: 570px) and (max-width: 940px) {
    .composition-source {
        padding-top: 430px;
    }
}

.composition-source-container {
    display: flex;
    place-items: center;
    background-color: #fff;
    transform-origin: 100% 0;
    overflow-x: hidden;
    overflow-x: hidden;
    min-height: 100%;
}

@media (max-width: 940px) {
    .composition-source-container {
        padding-top:0;
        margin-top: 0;
        place-items: start;
    }
}

@media (max-width: 480px) {
    .composition-source-container {
        padding-top: 20px
    }
}

