    text-align: initial;
} */

.composition-source-header {
    font-size: 86px;
    font-weight: 800;
    padding-top: 100px;
    padding-bottom: 100px;
    line-height: 0.3em;
    display: none;
    padding: 40px 20px 40px 40px;
}

.composition-source-header-active {
    display: block;
}

@media (max-width: 940px) {
    .composition-source-header {
        padding-top:30px;
        padding-bottom: 30px;
        font-size: 54px;
        line-height: 0.5em;
    }
}

@media (max-width: 480px) {
    .composition-source-header {
        /* display:none */
    }
}

.composition-source-text {
    font-size: 0.694rem;
    letter-spacing: 0.1em;
    color: #adadad;
    padding-bottom: 20px;
    margin: 0;
    border-bottom: 1px solid #eee;
}

.composition-source-subheader-container {
    display: flex;
    justify-content: space-between;
    gap: 40px;
}

.composition-source-subheader {
    font-size: 0.833rem;
    color: #adadad;
    text-transform: uppercase;

    &-left {
        text-align: left;
    }
    &-right {
        text-align: right;
        text-transform: capitalize;
    }
}

.composition-source-code {
    border: 0;
    cursor: text;
    font: 14px Consolas, "Courier New", Courier, monospace;
    outline: 0;
    resize: none
}

.highlight {
    background-color: #f4f4f6;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    padding: 2em 2em 2em 40px;
    position: relative;
    width: 100%;
    overflow-x: scroll;
    margin-bottom: 2em;
    line-height: 1.5em;
    box-sizing: border-box
}

@media (max-width: 480px) {
    .highlight {
        overflow-x:auto;
        overflow-wrap: break-word
    }
}

.hidden {
    display: none
}

.composition-back-button {
    position: fixed;
    top: 20px;
    left: 20px;
    z-index: 3002;
    display: none;
    -webkit-tap-highlight-color: transparent
}

.composition-back-image {
    width: 51px;
    height: 51px
}

@media (max-width: 940px) {
    .composition-back-button {
        position:absolute
    }
}

.collection-inline-header {
    font-weight: 200
}

@media (max-width: 420px) {
    .collection-inline-header {
        text-align:center
    }
